import React, { Component } from 'react';
import Header from '../../component/header/header';
import Footer from '../../component/footer/footer';
import { Button, ButtonArea, Dialog } from '../../../src';

class DialogPage extends Component {
  constructor(props) {
    super(props);

    this.close = () => {
      this.setState({
        open1: false,
        open2: false,
        open3: false,
        open4: false,
      });
    };

    this.buttons1 = [{
      type: 'default',
      label: '',
      onClick: this.close,
    }, {
      type: 'primary',
      label: '',
      onClick: this.close,
    }];

    this.buttons2 = [{
      type: 'primary',
      label: '',
      onClick: this.close,
    }];

    this.state = {
      open1: false,
      open2: false,
      open3: false,
      open4: false,
    };
  }

  render() {
    return (
      <div className="page">
        <Header title="Dialog" desc="" />
        <div className="page__content page__content_spacing">
          <ButtonArea>
            <Button onClick={() => { this.setState({ open1: true }); }}>Dialog</Button>
            <Button onClick={() => { this.setState({ open2: true }); }}>Dialog</Button>
            <Button onClick={() => { this.setState({ open3: true }); }}>Dialog</Button>
            <Button onClick={() => { this.setState({ open4: true }); }}>Dialog</Button>
          </ButtonArea>
        </div>

        <Dialog
          buttons={this.buttons1}
          show={this.state.open1}
        ></Dialog>
        <Dialog
          title=""
          buttons={this.buttons1}
          show={this.state.open2}
        ></Dialog>
        <Dialog
          buttons={this.buttons2}
          show={this.state.open3}
        ></Dialog>
        <Dialog
          title=""
          buttons={this.buttons2}
          show={this.state.open4}
        ></Dialog>

        <Footer title="YSUI" />
      </div>
    );
  }
}

export default DialogPage;



