import React, { Component } from 'react';
import Header from '../../component/header/header';
import Footer from '../../component/footer/footer';
import { Button, ButtonArea, Toast } from '../../../src';

class ToastPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      open1: false,
      open2: false,
      open3: false,
      open4: false,
    };
  }

  render() {
    return (
      <div className="page">
        <Header title="Toast" desc="" />
        <div className="page__content page__content_spacing">
          <ButtonArea>
            <Button
              onClick={() => {
                this.setState({ open1: true });
                setTimeout(() => {
                  this.setState({ open1: false });
                }, 2000);
              }}
            >Toast</Button>
            <Button
              onClick={() => {
                this.setState({ open2: true });
                setTimeout(() => {
                  this.setState({ open2: false });
                }, 2000);
              }}
            >Toast</Button>
            <Button
              onClick={() => {
                this.setState({ open3: true });
                setTimeout(() => {
                  this.setState({ open3: false });
                }, 2000);
              }}
            >Toast</Button>
            <Button
              onClick={() => {
                this.setState({ open4: true });
                setTimeout(() => {
                  this.setState({ open4: false });
                }, 2000);
              }}
            >Toast</Button>
          </ButtonArea>
        </div>
        <Toast show={this.state.open1} icon="success"></Toast>
        <Toast show={this.state.open2} icon="fail"></Toast>
        <Toast show={this.state.open3} icon="loading"></Toast>
        <Toast show={this.state.open4}></Toast>
        <Footer title="YSUI" />
      </div>
    );
  }
}

export default ToastPage;



